*,
*::after,
*::before{
    margin: 0;
}

nav {
    height: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.navbar-img {
    width: 160px;
}

.App {
    font-family: sans-serif;
}


/* .searchbar-container {
    display: flex;
    margin: 0px auto;
    padding: 0 20px;
    align-items: center; 
} */

.searchbar-container {
    display: flex;
    margin: 0px 10px;
    padding: 0;
    align-items: center;
    flex: 1;
    align-items: stretch;
  }

/* .searchbar {
    background: white;
    margin-right: 20px;
} */

.searchbar {
    background: white;
    padding-right: 20px;
    flex: 1;
  }

/* .searchbar input {
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
    border-radius: 3px;
    border: none;
} */

.searchbar input {
    padding: 10px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
    border-radius: 3px;
    border: none;
    width: 98%;
  }

.searchbar input:focus {
    outline: none;
}

.searchbar-btn button{
    background-color: #363636;
    border:none;
    border-radius: 10px;
    color: white;
    height: 44px;
    padding: 10px 12px;
}

.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.pokedex-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    padding: 0 10px;
}

@media screen and (max-width: 992px) {
    .pokedex-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media screen and (max-width: 600px) {
    .pokedex-grid {
      grid-template-columns: repeat(1, 1fr);
    }
  }

.pokemon-card {
    display: flex;
    box-shadow: 4px 4px 4px rgba(0 ,0 ,0 , 0.25);
    border-radius: 3px;
}

.pokemon-img-container{
    padding: 0px 5px;
}

.pokemon-img {
    width: 70px;
    height: 70px;
}

.pokemon-type{
    display: flex;
}

.pokemon-type-text{
    margin-right: 10px;
    text-transform: capitalize;
}

.card-body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px 4px 10px 10px;
    flex: 1;
}

.card-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.card-bottom{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.card-top h3{
    text-transform: capitalize;
}

.pagination {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.pagination-btn {
    background-color: black;
    border: none;
    border-radius: 50%;
    width: 28px;
    height: 28px;
    margin: 0px 8px;
    display: flex;
    align-items: center;
    justify-content: center; 
    cursor: pointer;
    font-size: 0px;
  }

.loading,
.notfound {
    margin-left: 10px;
    height: 100vh;
}

footer {
    font-family: inherit;
    background-color: #363636;
    padding: 10.2px;
    color: white;
    text-align: center;
    font-size: 1.1rem;
    margin-top: 10px;
}

.pokedex {
    min-height: 72vh;
}